@import 'styles/helpers';

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  position: relative;
  text-align: center;

  cursor: pointer;
  user-select: none;

  transition: box-shadow 300ms;
  border-radius: 8px;

  width: 65px;
  height: 65px;

  &:hover {
    box-shadow:0 3px 6px 0 rgba(0,0,0,.1),0 1px 3px 0 rgba(0,0,0,.08)
  }

  *{
    pointer-events: none;
  }
}

.mainSymbol {
  font-size: 30px;
  line-height: 1;
}

.info {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: 12px;
}

.secondarySymbol {
}

.romaji {
  display: inline-block;
  margin-right: 5px;
}

.itemOpened {
  composes: item;
  //opacity: 0;
  pointer-events: none;

  color: $accent-color;
  * {
    color: $accent-color;
  }
}